
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


import {formatCurrency} from '../unilt'

export default {

components: {
Flexbox,
FlexboxItem,
XButton,
XHeader
},
data() {
return {
money: 0,
month: 0,
rate: 0,
type: 1,
ringList: [],
houseTotalPrice: ''
}
},
created() {
this.money = Number(this.$route.query.sloanMoney)
this.month = Number(this.$route.query.smonths)
this.type = Number(this.$route.query.type)
let rate = Number(this.$route.query.rate)
let jx = Boolean(this.$route.query.jx)
if (jx) {//判断jx权重

}
if (this.type === 2) {//新车
rate += 0.3
} else {//二手车
rate += 0.4
}
this.rate = rate
},
mounted() {








},
filters: {
formatCurrency
},
computed: {
realRate() {
let constant = this.type === 2 ? 12 : 14.5
let base = this.month === 24 ? 3 : 0
return this.rate + (this.rate - constant + base) * .1
},
totalRate() {
console.log(this.realRate)
return 1 + this.realRate * .01
},
realSplitAmount() {
let base = this.month === 24 ? 0.03 : 0
console.log((this.money * this.totalRate / (1.09 - base)).toFixed(2))
return (this.money * this.totalRate / (1.09 - base)).toFixed(2)
},
splitAmount() {
return Math.ceil(this.realSplitAmount / 100) * 100
},
monthRepayAmount() {
return Math.floor(this.splitAmount / this.month)
},
monthRepayInterest() {
return Math.floor(this.bankCost / this.month)
},
totalRepay() {
let base = this.month === 24 ? 0.03 : 0
return (this.splitAmount * (1.09 - base)).toFixed(2)
},
bankCost() {
return this.totalRepay - this.splitAmount
},
comCost() {
return this.splitAmount - this.money
},
monthRepay() {
return this.monthRepayAmount + this.monthRepayInterest
},
firstMonthRepay() {
return this.totalRepay - this.monthRepay * this.month
},
cardInsured() {
return Math.ceil((this.splitAmount / 0.7 / 1.2) / 100) * 100
},
invoiceAmount() {
return Math.ceil(this.splitAmount / 0.8 / 1000) * 1000
}
},
methods: {},
}



